import React, {Component} from 'react';
// import $ from 'jquery';
import './Djs.css';
 class CountDown extends Component {
   //设置初始状态time
    constructor(props){
      super(props);
      this.state ={
        time : 120
      }
    }
    //钩子函数里调用this.startCheckStatus()方法
  componentDidMount() {　
    this.startCheckStatus()
  }
        startCheckStatus() {
          let interval = setInterval(() => {
            if (this.state.time == 0) {
              clearInterval(interval);
              return;
            }
            //设置时间的改变
            this.setState({time:--this.state.time
            , 
            })
            // this.startCheckStatus()
            // this.getOrderStatus();
          }, 1000);
          console.log(interval)
        }
  render() {
    return (
       <p>支付码失效还有:<span>{this.state.time}s</span></p>
    )
  }
 }
export default CountDown;



