import React, { Component } from 'react'
import './App.css'
import axios from 'axios'
import CountDown from './Djs/Djs'
import Mock from 'mockjs'
class App extends Component {
  state = {
    id: '',
    qrcodeUrl: '',
    status: 0,
    isShow: true
  }
  componentDidMount () {
    let id = getQueryString('id')
    let qrcodeUrl = getQueryString('qrcodeUrl')
    this.setState({
      id: id,
      qrcodeUrl: qrcodeUrl,
      status: 0
    })
    this.startCheckStatus()
  }

  getOrderStatus () {
    axios({
      method: 'post',
      url: '/order/query',
      data: {
        id: this.state.id
      },
      responseType: 'json'
    }).then(res => {
      let resData = res.data
      if (resData.code == 2) {
        this.setState({
          status: resData.result.status,
          isShow: false
        })
      }
    })
  }

  startCheckStatus () {
    let interval = setInterval(() => {
      if (this.state.status == 2) {
        clearInterval(interval)
        return
      }
      this.getOrderStatus()
    }, 5000)
  }

  render () {
    return (
      <div className='App'>
        <div className={this.state.isShow ? 'show' : 'hide'}>
          <div className='tpk'>
            <img src={this.state.qrcodeUrl} alt='' />
          </div> <div className='Djs'>
            <CountDown />
          </div>
        </div>

        <div className={this.state.isShow ? 'hide' : 'show'}>
          支付成功
        </div>
      </div>
    )
  }
}

function getQueryString (name, url) {
  url = url || window.location.search
  var reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)', 'i')
  var r = url.substr(1).match(reg)
  if (r != null) {
    return unescape(r[2])
  }
  return null
}
